import React from "react";
import { render, fireEvent, waitFor } from "@testing-library/react-native";
import Login from "../screens/inscription/Login";

// Mock navigation
jest.mock("@react-navigation/native", () => ({
  useNavigation: () => ({ navigate: jest.fn() }),
}));

// Mock Toast
jest.mock("react-native-toast-message", () => ({
  show: jest.fn(),
}));

// Mock axios
import axios from "axios";
jest.mock("axios");

describe("Login Component - Simulation page de dépôt", () => {
  it("rend correctement les champs et le bouton", () => {
    const { getByPlaceholderText, getByText } = render(<Login />);

    expect(getByPlaceholderText("Entrer votre email")).toBeTruthy();
    expect(getByPlaceholderText("Entrer votre mot de passe")).toBeTruthy();
    expect(getByText("Se connecter")).toBeTruthy();

    console.log("✅ Les champs de dépôt sont affichés correctement");
  });

  it("permet de saisir l'email et le mot de passe", () => {
    const { getByPlaceholderText } = render(<Login />);
    const emailInput = getByPlaceholderText("Entrer votre email");
    const passwordInput = getByPlaceholderText("Entrer votre mot de passe");

    fireEvent.changeText(emailInput, "test@example.com");
    fireEvent.changeText(passwordInput, "123456");

    expect(emailInput.props.value).toBe("test@example.com");
    expect(passwordInput.props.value).toBe("123456");

    console.log("✅ Les valeurs du dépôt ont été saisies correctement");
  });

  it("appelle l'API et affiche un toast en cas de succès", async () => {
    const mockResponse = { status: 200, data: { message: "Connexion réussie" } };
    axios.post.mockResolvedValueOnce(mockResponse);

    const { getByText, getByPlaceholderText } = render(<Login />);
    const emailInput = getByPlaceholderText("Entrer votre email"); // conserver le placeholder d'origine
    const passwordInput = getByPlaceholderText("Entrer votre mot de passe");

    fireEvent.changeText(emailInput, "test@example.com");
    fireEvent.changeText(passwordInput, "123456");

    fireEvent.press(getByText("Se connecter"));

    await waitFor(() => {
      const Toast = require("react-native-toast-message");
      expect(Toast.show).toHaveBeenCalledWith(
        expect.objectContaining({ type: "success" })
      );

      console.log("✅ Dépôt simulé avec succès, toast affiché");
    });
  });
});
